.nav_conteiner {
  display: flex;
  border-bottom: 1px solid black;
  padding-bottom: 20px;
  background-color: rgb(255, 192, 110);
}
.navigate {
  flex-wrap: wrap-reverse;
  font-weight: bold;
  color: blue;
  text-transform: uppercase;
  margin-left: 20px;
  margin-top: 20px;
  text-decoration: none;
  padding: 7px;
}
.navigate:hover,
.navigate:focus {
  background-color: blue;
  color: white;
  border-radius: 5px;
}
